    display: none;
  }
}
.page-header__trigger--nojs {
  display: none;
}
.page-header__trigger-name {
  position: absolute;
  content: "";
  top: 8px;
  left: 0;
  background: #222222;
  width: 100%;
  height: 2px;
  transition: 0.3s;
  font-size: 0;
}
.page-header__trigger-name::before,
.page-header__trigger-name::after {
  position: absolute;
  content: "";
  top: -8px;
  left: 0;
  background: #222222;
  width: 100%;
  height: 2px;
  transform-origin: 50% 50%;
  transition: 0.3s 0.3s, transform 0.3s;
}
.page-header__trigger-name::before {
  top: 8px;
}
.page-header__trigger-name::after {
  top: -8px;
}
.page-header__trigger--close .page-header__trigger-name {
  background: transparent;
}
.page-header__trigger--close .page-header__trigger-name::before,
.page-header__trigger--close .page-header__trigger-name::after {
  transform: rotate3d(0, 0, 1, 45deg);
  transition: 0.3s, transform 0.3s 0.3s;
  top: 0;
}
.page-header__trigger--close .page-header__trigger-name::before {
  transform: rotate3d(0, 0, 1, -45deg);
}
